* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
    -webkit-tap-highlight-color: transparent;
}
Style Attribute {
    font-size: 16px;
}
.SQtZc .header-bd {
    width: 100%;
    padding-right: 9px;
    padding-left: 42px;
    padding-top: 6px;
    padding-bottom: 6px;
    background-image: url(data:image/png;base64,);
    background-size: 27px 20px;
    background-repeat: no-repeat;
    background-position: 9px center;
}
.kuVLqC {
    position: relative;
    width: 100%;
    height: 100%;
}
.SQtZc {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
.SQtZc .header-bd .placeholder .text::before {
    font-size: 15px;
    display: inline-block;
    color: rgb(255, 255, 255);
    margin-right: 5px;
}
header {
    display: block;
}
.SQtZc .header-bd .placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
a {
    text-decoration: none;
}
.iconfont {
    font-family: h5index-iconfont;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
}
.SQtZc .header-bd .placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.text{
    color: rgb(240, 224, 220);
}